<template>
  <div class="info">
    <div class="pt-16">
      <div class="bg--grey">
        <div class="bg--wrap">
          <div class="title title-regular">
            <h3>åŸºæœ¬æƒ…å ±ç™»éŒ²</h3>
            <p>
              å¤–å›½å¼æ°åãŒæˆ¸ç±ã«è¨˜è¼‰ã•ã‚Œã¦ã„ã‚‹å ´åˆã€å›½éš›çµå©šã«ã‚ˆã‚Šæˆ¸ç±ä¸Šã®å§“ãŒå¤–å›½å¼ã®å§“ã¨ãªã£ã¦ã„ã‚‹ã€ã‚‚ã—ãã¯é‡å›½ç±ã§æˆ¸ç±ä¸Šã®æ°åãŒå¤–å›½å¼ã®æ°åã¨ãªã£ã¦ã„ã‚‹å ´åˆã€æˆ¸ç±ä¸Šã®ç¶´ã‚Šã§å…¥åŠ›ã—ã¦ãã ã•ã„ã€‚
            </p>
          </div>
          <div class="pt-24" v-for="(item, index) in info" :key="index">
            <div class="required" v-if="item.isRequired">
              <p>å¿…é ˆ</p>
            </div>
            <p
              class="required-detail color--black"
              :class="{ 'p-0': !item.isRequired }"
            >
              {{ item.detail }}
            </p>
            <div class="pt-8">
              <InputComponent
                :field="item"
                v-if="item.type == 'text'"
                @changeMessage="changeMessage"
                :doneFill="doneFill"
              ></InputComponent>
              <SelectChoose
                v-else-if="item.type == 'choose'"
                :field="item"
                :doneFill="doneFill"
                @changeRadioChoose="changeRadioChoose"
              ></SelectChoose>
              <DateComponent
                v-else-if="item.type == 'date'"
                :field="item"
                @changeMessage="changeMessage"
                :doneFill="doneFill"
              ></DateComponent>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import InputComponent from "./InputComponent.vue";
import SelectChoose from "./selectChoose.vue";
import DateComponent from "./dateComponent.vue";
import { mapGetters } from "vuex";
export default {
  props: {
    doneFill: Boolean
  },
  components: {
    InputComponent,
    SelectChoose,
    DateComponent
  },
  computed: {
    ...mapGetters({
      info: "getUserInfo"
    })
  },
  methods: {
    changeMessage(value) {
      this.$store.dispatch("updateInfoData", value);
    },
    changeRadioChoose(value) {
      this.$store.dispatch("updateInfoData", value);
    }
  }
};
</script>
<style scoped></style>



